@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*,::before,::after{font-family:'Lato';box-sizing:border-box}body{padding:20px 40px}*::-webkit-scrollbar{width:5px}*::-webkit-scrollbar-track{box-shadow:inset 0 0 6px rgba(0,0,0,0.3)}*::-webkit-scrollbar-thumb{background-color:darkgrey;outline:1px solid slategrey}a{text-decoration:none;color:black}

.collection-item{position:relative}.collection-item .custom-button{position:absolute;top:255px;opacity:0;-webkit-transition:100ms ease-in-out;transition:100ms ease-in-out;color:black;border-radius:2px;font-size:20px}.collection-item:hover .custom-button{opacity:0.85;-webkit-transition:200ms ease-in-out;transition:200ms ease-in-out}.collection-item:hover .custom-button:hover{color:white}

.group{position:relative;margin:45px 0}.group .form-input{background:none;background-color:white;color:grey;font-size:18px;padding:10px 10px 10px 5px;display:block;width:100%;border:none;border-radius:0;border-bottom:1px solid grey;margin:25px 0}.group .form-input:focus{outline:none}.group .form-input:focus ~ .form-input-label{top:-14px;font-size:12px;color:#000}.group input[type='password']{letter-spacing:0.3em}.group .form-input-label{color:grey;font-size:16px;font-weight:normal;position:absolute;pointer-events:none;left:5px;top:10px;-webkit-transition:300ms ease all;transition:300ms ease all}.group .form-input-label.shrink{top:-14px;font-size:12px;color:#000}

